body
{
   background-color: #373736;
   color: #f6f5f3
}

a
{
   color: #000
}

#page-footer a, h1 > a
{
   color: #f6f5f3
}

table thead th
{
   border-bottom-color: #373736
}

#page-content
{
   background-color: #f6f5f3;
   color: #373736
}

#page-content h3
{
   border-bottom-color: #ccc
}

#page-navigation
{
   background-color: #e6e5e3;
   border-bottom-color: #373736;
   color: #373736;
}

#page-navigation button
{
   color: #373736
}

#page-navigation #page-navigation-links > li a
{
   color: #373736
}

#page-navigation #page-navigation-links > li.open > a, #page-navigation #page-navigation-links > li > a:hover
{
   background-color: #f6f5f3
}

.dropdown-menu
{
   background-color: #e6e5e3;
   border-color: #373736;
   box-shadow: 0 5px 10px rgba(0, 0, 0, .15)
}

.dropdown-menu a:active, .dropdown-menu a:focus, .dropdown-menu a:hover
{
   background-color: #f6f5f3
}

article
{
   box-shadow: none
}

article h1, article h3
{
   color: #242423
}

article .build-details-comments a, article .build-group > h3 > a, article a.more-link
{
   color: #242423
}

ul.pagination > li.active > a
{
   background: #373736;
   color: #e6e5e3 !important
}

ul.pagination > li:active:not(.disabled) > a, ul.pagination > li:focus:not(.disabled) > a, ul.pagination > li:hover:not(.disabled) > a
{
   background: #242423;
   color: #f6f5f3 !important
}

#modal-search-overlay #modal-search
{
   background-color: #f6f5f3
}

#modal-search-overlay #modal-search h3
{
   color: #373736
}

#modal-search-overlay #modal-search > #modal-search-box > #modal-search-button, #modal-search-overlay #modal-search > #modal-search-box > #modal-search-input
{
   background-color: #fff;
   border-color: #242423;
   color: #373736;
}
